import React, { Component } from 'react';

class ContenedorHistoria extends Component {

  render() {
    //Creo una variable donde voy a guardar todas las props
    const { historia, id, opciona, opcionb, handleClick } = this.props

    return (
        <div className="contenedorHistoria" key={id}>
          <div>
            <h2 className="historia">{historia}</h2>
          </div>
          <div className="opciones">
            <div className="opcion">
              <button id="A" className="botones" onClick={handleClick}>A</button>
              <h3>{opciona}</h3>
            </div>
            <div className="opcion">
              <button id="B" className="botones" onClick={handleClick}>B</button>
              <h3>{opcionb}</h3>
            </div>
          </div>
        </div>
    )
  }
}

export default ContenedorHistoria