import React, { Component } from 'react';
import ContenedorHistoria from './Contenedor.jsx';
import Contador from './Contador.jsx';
import data from './data.json';
import Swal from 'sweetalert2'

class Pantalla extends Component {

  constructor(props) {
    super(props);
    this.state = {
      todasHistorias: data,
      ultimaEleccion: "",
      historialElecciones: [],
      contadorElecciones: 0,
    }
  }

  //Renderiza el componente en el DOM por primera vez -- ¿Es realmente necesario?
  // static getDerivedStateFromProps() {
  //   this.setState({ todasHistorias: data })
  //   console.log("Renderice el componente por primera vez")
  // }

  //Para operar en el DOM con el componente actualizado
  //En este caso me dice que si mi contador tiene un valor distinto al anterior, que pushee al historial de elecciones 
  componentDidUpdate(prevProps, prevState) {
    let historial = this.state.historialElecciones
    if (prevState.contadorElecciones !== this.state.contadorElecciones) {
      historial.push(this.state.ultimaEleccion);
    }
  }

  //Elimina el componente del DOM
  componentWillUnmount() {
    console.log("Adios componente");
    clearInterval(this.state.historialElecciones);
  }

  //Aca sucede toda la magia
  handleClick = (e) => {
    let idBtn = e.target.id
    let ultima = this.state.ultimaEleccion
    this.setState({ ultimaEleccion: idBtn })

    //Todas B => Contador: 8
    //Todas A => Contador: 7
    //Tengo 16 combinaciones posibles

    if (this.state.contadorElecciones >= 7) {

      // alert("Hemos llegado al final de la aventura")
      Swal.fire('Hemos llegado al final de la aventura')
    }

    //OPCION 1: Elegí A y antes B (tengo que avanzar solo una "página")
    else if (idBtn === "A" && ultima === "B") {
      this.setState({ contadorElecciones: this.state.contadorElecciones + 1 })
    }

    //OPCION 2: Elegí la misma que antes (tengo que avanzar dos "páginas")
    else if ((idBtn === "A" && ultima === "A") || (idBtn === "B" && ultima === "B")) {
      this.setState({ contadorElecciones: this.state.contadorElecciones + 2 })
    }

    //OPCION 3: Elegí B y antes A (tengo que avanzar tres "páginas")
    else if (idBtn === "B" && ultima === "A") {
      this.setState({ contadorElecciones: this.state.contadorElecciones + 3 })
    }

    //OPCION 4: El comienzo
    else if (idBtn === "A" && ultima === "") {
      this.setState({ contadorElecciones: this.state.contadorElecciones + 1 })
    }

    //OPCION 5: El comienzo
    else if (idBtn === "B" && ultima === "") {
      this.setState({ contadorElecciones: this.state.contadorElecciones + 2 })
    }
    console.log('el boton fue pulsado');
    console.log(this.state.historialElecciones);
    console.log(this.state.contadorElecciones)
  }


  render() {
    let i = this.state.contadorElecciones
    let historialElecciones = this.state.historialElecciones
    return (
      <div className="App">
        <main className="layout">
          <ContenedorHistoria handleClick={this.handleClick} key={this.state.todasHistorias[i].id} historia={this.state.todasHistorias[i].historia} opciona={this.state.todasHistorias[i].opciones.a} opcionb={this.state.todasHistorias[i].opciones.b} />

          <Contador prev={this.state.ultimaEleccion} hist={historialElecciones.map((cont, pos) => <li key={pos}>{cont}</li>
          )} />
        </main>
      </div>
    )
  }
};

export default Pantalla;